.Modal {
  position: fixed;
  z-index: 200;
  border: 1px solid #eee;
  -webkit-box-shadow: 0 2px 2px #ccc;
          box-shadow: 0 2px 2px #ccc;
  background-color: white;
  padding: 10px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  top: 30%;
  left: 25%;
  width: 50%;
  /* transition: all 0.3sn ease-out; */
}

.ModalShow {
  -webkit-animation: ferhatOpen 0.6s ease-out forwards;
          animation: ferhatOpen 0.6s ease-out forwards;
}

.ModalClosed {
  -webkit-animation: ferhatClose 1s ease-out forwards;
          animation: ferhatClose 1s ease-out forwards;
}
.fade-slide-enter {
}

.fade-slide-enter-active {
  -webkit-animation: ferhatOpen 0.6s ease-out forwards;
          animation: ferhatOpen 0.6s ease-out forwards;
}

.fade-slide-exit {
}

.fade-slide-exit-active {
  -webkit-animation: ferhatClose 1s ease-out forwards;
          animation: ferhatClose 1s ease-out forwards;
}

@-webkit-keyframes ferhatOpen {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  50% {
    opacity: 0.4;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes ferhatOpen {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  50% {
    opacity: 0.4;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes ferhatClose {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    opacity: 0.8;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
}

@keyframes ferhatClose {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    opacity: 0.8;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
}



